.poppins{
  font-family:'Poppins';
  font-weight:700;
}
.turqs{
  color:#39cc91 !important;
}
.nav-logo{
  margin:0 40px;
  font-size:30px;
}
.nav .link-style{
  text-decoration: none;
}
.align-to-logo{
  margin-top:9px;
}
.open-sans{
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
}
.gray{
   color:gray;
}
.main{
  padding:10px
}
.display-flex{
  display: flex;
  max-width:80vw;
  width:80vw;
}
.white{
  background-color: white;
}
.no-background{
  background-color: none;
}
.align-end{
  justify-content: flex-end;
}
.parent {
  position: relative;
}
.child {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -120%);
  padding-left: 5%;
  padding-right:5%
}
.align-center{
  margin:0 auto;
  text-align: center;
}
.blue-background{
  background-color: #007bff;
  border:none;
}
.hero-margin-top{
  margin-top:10%
}
.button-margin-top{
  margin-top:5%
}
.big-button{
  width:10%;
}
.extra-large{
  font-size:5vw;
  padding-left:50px;
  padding-right:50px;
}
.logo-size{
  display: inline;
  height: auto;
  max-width: 100%;
}
.poppins-italics{
  font-family: 'Poppins', sans-serif;
  font-style: italic;
  font-size:18px;
  font-weight: 600;
}
.logo{
  font-family: 'Poppins', sans-serif;
  font-size:24px;
  font-weight: 600;
}
.background-image-hero{
  background-image: url('./Assets/berries.jpg');
